<template>
    <div>
       <mt-header title="">
        <!-- <router-link to="/usercenter" slot="left"><mt-button icon="back"></mt-button></router-link> -->
       </mt-header>
        <div class="duigou">
            <div></div>
            <p>ID<span v-text="userData.userID"></span></p>
        </div>
        <p class="sh-ts">
            3
        </p>
        <p class="mm-ts"></p>
        <router-link class="btn" to="/"></router-link>
    </div>
</template>
<script>
import store from '../store/store'
import {mapState} from 'vuex'
export default {
    name:'spinfoSuccess',
    store,
    computed:mapState(['userData'])
}
</script>
<style>
.duigou{
    width:55%;
    margin:20px auto;
    color:#ff5f28;
    font-family:'';
    overflow: hidden;
}
.duigou div{
    width:40px;
    height:40px;
    background:url(../../static/images/duigou.png) no-repeat;
    background-size:100%;
    float:left;
}
.duigou span{
    color:#f43205;
}
.sh-ts{
    text-align: left;
    padding:10px 8px;
}
.mm-ts{
    width:90%;
    margin:10px auto;
    color:#ff0006;
}
.btn{
    display:inline-block;
    border-radius:5px;
    width:100px;
    padding:10px 0;
    background:#ff5f28;
    color:#ffffff;
}
</style>




